@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Poppins-Regular.woff2") format("woff2"), url("../fonts/Poppins-Regular.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Poppins-Medium.woff2") format("woff2"), url("../fonts/Poppins-Medium.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/Poppins-SemiBold.woff2") format("woff2"), url("../fonts/Poppins-SemiBold.woff") format("woff");
}
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Poppins-Bold.woff2") format("woff2"), url("../fonts/Poppins-Bold.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  display: flex;
  margin: 0;
  min-height: 100%;
  flex-direction: column;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  background-image: url("../img/hero-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.page__main {
  flex-grow: 1;
}
.document {
  height: 100%;
}
.header {
  padding: 30px 0;
  margin-bottom: 320px;
}
.navigation {
  display: flex;
  justify-content: center;
  align-items: center;
}
.navigation__logo {
  display: block;
  margin-right: 65px;
}
.navigation__link {
  font-size: 18px;
  line-height: 27px;
  font-weight: 600;
  text-decoration: none;
  color: #ffffff;
}
.navigation__list {
  display: flex;
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.navigation__item {
  margin-right: 56px;
}
.hero {
  display: flex;
  margin: 0 auto;
  flex-direction: column;
  width: 1110px;
  text-transform: capitalize;
}
.hero__pretitle {
  font-weight: 700;
  font-size: 48px;
  line-height: 72px;
  color: #ffffff;
}
.hero__single-name {
  font-weight: 700;
  font-size: 100px;
  line-height: 150px;
  color: transparent;
  -webkit-text-stroke-color: #ffffff;
  -webkit-text-stroke-width: 1px;
}
